html {
  font-size: 62.5%;
}
body {
  font-family: 'Fira Sans', Helvetica, sans-serif;
  font-size: 1.4rem;
  color: #333;
  background-repeat: repeat-x;
  background-color: #f8f8f8;
}
a {
  text-decoration: none;
  color: #0178A3
}
a:hover {
  text-decoration: underline
}
.Header_fullWidth {
  background-color: #222830;
}

.Header_header {
  max-width: 1140px;
  min-width: 960px;
  height: 60px;
  padding: 0 20px;
  margin: auto;
}

#Header_menu {
  height: 80px;
  display: inline-block;
  float: left;
}
#Header_menu_bar {
    height: 78px;
    float: left;
    overflow: hidden;
    margin-top: 2px
}
#Header_search_box {
    float: right;
    height: 30px;
    padding-top: 26px;
    text-align: right;
    width: 350px;
    display: inline-block;
}
#Header_search_box input .Header_search_txt_input {
    border: 1px solid #C9C8C3;
    color: #333;
    font-size: 14px;
    height: 18px;
    margin-right: 10px;
    width: 210px
}
#Header_search_box input .Header_search_submit {
    width: 92px;
    float: right!important
}
#Header_user_log_form a {
    font-size: 12px
}
#Header_user_log_form p {
    padding: 10px
}
#Header_user_log_form  .Header_usr_input {
    width: 230px
}
#Header_user_log_form #Header_pl_log_submit {
    float: none;
    position: static;
    text-transform: uppercase;
    font-size: 12px
}
.UserAccount_container {
  float: right;
}

.UserAccount_button {
  margin: 11px 10px
}
.Button_base {
  background-color: #54b1c4;
  border: none;
  border-radius: 3px;
  color: white;
  font-size: 1.4rem;
  font-weight: bold;
  padding: 10px 20px;
  outline: none;
  text-transform: uppercase;
}

.Button_base:hover {
  background-color: #334a5c;
  color: #54b1c4;
}

.Button_base[disabled] {
  background-color: #334a5c;
  color: #54b1c4;
}

.Button_secondary {
  background-color: #f1f1f1;
  color: #54b1c4;
}

.Button_secondary:hover {
  background-color: #54b1c4;
  color: #fff;
}

.Button_secondary[disabled] {
  background-color: #54b1c4;
  color: #fff;
}

.Button_passive {
  background-color: #334a5c;
  color: #54b1c4
}

.Button_passive:hover {
  color: #496d89;
}

.Button_passive[disabled] {
  color: #496d89;
}
.Logo_logo {
  padding: 0 20px 0 0;
  float: left;
  margin-top: 18px;
  display: inline-block;
}

.Logo_logo img {
  width: 180px;
}
.Menu_nav {
  padding-left: 3px;
  margin-top: 1px;
}
.Menu_nav ul {
  margin: 0;
  list-style: none;
  padding: 0;
}
.Menu_nav ul li {
  float: left;
  text-align: center;
  display: block
}
.Menu_nav>ul>li>a {
  color: #FFF;
  cursor: pointer;
  display: inline-block;
  font-size: 1.4rem;
  font-weight: bold;
  height: 23px;
  padding: 24px 4px 13px 0;
  text-decoration: none;
  vertical-align: middle;
  width: 108px;
}
.Menu_nav>ul>li:hover {
  background-color: #334b5c;
}
.Menu_nav>ul>li>ul {
  background-color: #2e353e;
  color: #FFF;
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  top: 61px;
  z-index: 999;
  min-width: 960px;
  margin: auto;
}
.Menu_nav>ul>li>ul>li {
  width: 25%;
  text-align: left;
}
.Menu_nav>ul>li>ul>li:hover {
  background-color: #4a6275;
}

.Menu_nav>ul>li>ul>li>a {
  color: #61b1c6;
  cursor: pointer;
  display: block;
  padding: 20px;
}
.Menu_nav>ul>li>ul>li>a:hover {
  color: white;
  text-decoration: none;
}
.Menu_nav ul li:hover>ul {
    display: block
}
.SearchBox_searchBox {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
  position: relative;
}

.SearchBox_input {
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid #c2c5c7;
  color: #ebebeb;
  padding: 10px 50px 10px 10px;
}

.SearchBox_icon {
  position: absolute;
  right: 11px;
  top: 9px;
}
.Icon_icon {
  fill: currentColor;
  display: inline-block;
}
.Category_base {
  background-color: #54b1c4;
  border-radius: 50%;
  float: left;
  height: 20px;
  width: 20px;
  color: white;
  text-align: center;
}

.Category_base i {
  margin-top: 3px;
  display: inline-block;
}

.Category_arte-y-narrativa {
  background-color: #b45f4a;
}

.Category_devocionales {
  background-color: #54b1c4;
}

.Category_dinamicas-y-juegos {
  background-color: #ecad29;
}

.Category_estudios-biblicos {
  background-color: #74c97f;
}

.Category_consejeria {
  background-color: #d0783a;
}

.Category_cursos {
  background-color: #4469c7;
}

.Category_libros {
  background-color: #6dbc9f;
}

.Category_reflexiones {
  background-color: #b891c4;
}

.Category_tecnologia {
  background-color: #515f74;
}

.Category_articulos {
  background-color: #526173;
}

.Category_musica {
  background-color: #526176;
}

.Category_videos {
  background-color: #b7b7b7;
}
.Footer_fullWidth {
  background-color: #2e353e;
}

.Footer_footer {
  max-width: 1140px;
  min-width: 960px;
  padding: 30px 20px 20px;
  margin: auto;
}
.Menu_nav {
  display: inline-block;
  /* width: 100%; */
}

.Menu_menu {
  color: white;
  list-style: none;
  margin: 0;
  padding: 0;
}

.Menu_menu li {
  display: inline-block;
  float: left;
  margin-bottom: 15px;
  text-align: center;
  width: 25%;
}

.Menu_menu li a {
  color: white;
}
.Copy_copy {
  background-color: #222830;
  color: white;
  font-size: 1.2rem;
  padding: 20px;
  text-align: center;
}
.Main_container {
  max-width: 1140px;
  min-width: 960px;
  padding: 0 20px;
  margin: auto;
}
.Resource_section {
  max-width: 900px;
  margin: auto;
  margin-top: 30px;
}

.Resource_header {
  display: inline-block;
  width: 100%;
}

.Resource_author {
  width: 40px;
  height: 40px;
  background-color: #ccc;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  color: #bbb;
  text-align: center;
  line-height: 5.5rem;
}

.Resource_authorName {
  font-weight: 400;
  margin-bottom: 0px;
  margin-left: 50px;
  margin-top: 8px;
  text-transform: uppercase;
}

.Resource_authorName a {
  color: #bbb;
}

.Resource_date {
  font-size: 1.2rem;
  color: #aaa;
  text-transform: uppercase;
  margin-left: 50px;
}

.Resource_actions {
  display: inline-block;
  float: right;
  margin-top: 30px;
}

.Resource_action {
  display: inline-block;
  width: 42px;
  height: 42px;
  background-color: #ececec;
  color: #747474;
  overflow: hidden;
  border-radius: 50%;
  float: left;
  font-size: 3rem;
  text-align: center;
  line-height: 4.2rem;
  margin-right: 15px;
}

.Resource_category {
  display: inline-block;
  text-transform: uppercase;
  margin-top: 30px;
}

.Resource_categoryIcon {
  display: inline-block;
  float: left;
  margin: -6px 3px 2px 0px;
}

.Resource_categoryLink {
  color: #54b1c4;
  display: inline-block;
  float: left;
  margin-left: 6px;
  margin-top: 3px;
}

.Resource_resourceTitle {
  display: inline-block;
  font-size: 3.2rem;
  font-weight: 500;
  line-height: 3.8rem;
  margin-bottom: 8px;
  margin-top: 0px;
  width: 100%;
}

.Resource_review {
  font-size: 1.9rem;
  line-height: 1.58;
  letter-spacing: 0.013rem;
  display: inline-block;
  margin-top: 10px;
}

.Resource_reviewTitle {
  margin: 8px 0px;
  text-transform: uppercase;
  color: #334a5c;
  display: inline;
  margin-right: 10px;
}

.Resource_download {
  display: inline-block;
  float: left;
  margin: 30px 0;
}

.Resource_content {
  font-size: 1.9rem;
  line-height: 1.58;
  display: inline-block;
  margin-top: 40px;
  letter-spacing: 0.013rem;
  padding-top: 40px;
  border-top: 1px solid #dedede;
}

.Resource_contentTitle {
  margin: 8px 0px;
  text-transform: uppercase;
  color: #334a5c;
  display: inline;
  margin-right: 10px;
}

.Resource_embededResourceContainer {
  display: inline-block;
  margin: 20px 0;
  height: 700px;
  background-color: #f4f4f4;
}

.Resource_tagContainer {
  display: inline-block;
  padding: 20px;
}
.Tag_base {
  background-color: #edeef0;
  border-radius: 3px;
  color: #2e353e;
  cursor: pointer;
  display: inline-block;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 10px;
  margin: 10px 10px 0 0;
  min-width: 120px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}

.Tag_base:hover {
  background-color: #2e353e;
  color: white;
}
.ExtraResources_title {
  border-bottom: 1px solid #ddd;
  font-size: 3.5rem;
  text-align: center;
  font-weight: 300;
  margin: 30px 0;
  padding-bottom: 20px;
}
